import { Container, Row, Col, Button } from "react-bootstrap";
import { MailchimpForm } from "./MailchimpForm";
import logo from '../assets/img/Trishnalogo.png';
import navIcon1 from "../assets/img/nav-icon1.svg";
import navIcon2 from "../assets/img/nav-icon2.svg";
import navIcon3 from "../assets/img/nav-icon3.svg";
import brochurePdf from "../assets/pdf/BOT-A-MAZE-RULES.pdf";
import posterPdf from "../assets/pdf/TRISHNA 2K24 POSTER.pdf";
import certificatePdf from "../assets/pdf/UPDATE SOON.pdf";
import schedulePdf from "../assets/pdf/UPDATE SOON.pdf"; // New PDF for Schedule
import bonafidePdf from "../assets/pdf/UPDATE SOON.pdf"; // New PDF for Bonafide
import { useState } from "react";

export const Footer = () => {
  const [secondButtonColor, setSecondButtonColor] = useState("primary");

  const handleDownloadBrochure = () => {
    window.open(brochurePdf, "_self");
  };

  const handleViewPoster = () => {
    window.open(posterPdf, "_self");
    setSecondButtonColor("danger"); // Change color to red on click
  };

  const handleViewModelCertificate = () => {
    window.open(certificatePdf, "_self");
  };

  const handleViewSchedule = () => {
    window.open(schedulePdf, "_self"); // New handler for Schedule
  };

  const handleViewBonafide = () => {
    window.open(bonafidePdf, "_self"); // New handler for Bonafide
  };

  return (
    <footer className="footer">
      <Container>
        <Row className="align-items-center">
          <MailchimpForm />
          <Col size={12} sm={6}>
            <div className="d-flex flex-column align-items-start">
              <img src={logo} alt="Logo" style={{ width: '240px', height: 'auto', marginBottom: '7px', marginLeft: '-60px' }} />
              <span className="logo-text">TRISHNA 2K24</span>
            </div>
          </Col>
          <Col size={12} sm={6} className="text-center">
            <div className="d-flex justify-content-center mt-3">
              {/* First row of 3 buttons */}
              <div>
                <Button variant="warning" className="m-2" onClick={handleDownloadBrochure}>
                  Download Brochure
                </Button>
                <Button variant={secondButtonColor} className="m-2" onClick={handleViewPoster}>
                  View Poster
                </Button>
                <Button variant="success" className="m-2" onClick={handleViewModelCertificate}>
                  View Certificate Model
                </Button>
              </div>
            </div>
            {/* Second row of 2 buttons */}
            <div className="d-flex justify-content-center mt-3">
              <Button variant="info" className="m-2" onClick={handleViewSchedule}>
                View Schedule
              </Button>
              <Button variant="danger" className="m-2" onClick={handleViewBonafide}>
                View Bonafide
              </Button>
            </div>
            <div className="social-icon mt-3">
              <a href="https://www.linkedin.com/in/trishna-tech-fest-2k24/"><img src={navIcon1} alt="Icon" /></a>
              <a href="https://www.facebook.com/trishna2k24?mibextid=ZbWKwL"><img src={navIcon2} alt="Icon" /></a>
              <a href="https://www.instagram.com/trishna_aitt?igsh=ZTZ0ajBvejN0aTg4"><img src={navIcon3} alt="Icon" /></a>
            </div>
            <p className="text-center mt-3">Copyright 2024. All Rights Reserved</p>
            <p className="text-center">Created by <br></br> <br></br> B. Guru Gangadhar Reddy - 21AK1A0427 <br></br> <br></br> D. Mahammed Kaif - 21AK1A0467</p>
          </Col>
        </Row>
      </Container>
    </footer>
  );
}
